.swap {
    padding: 50px 40px 80px;
    .bg {
        width: 460px;
        padding: 20px 40px;
        margin: 0 auto;
        margin-top: 25px;
        margin-bottom: 25px;
        background: #FAFAFA;
        border-radius: 48.5px;

        .input {
            width: 200px;
            font-family: Helvetica;
            font-size: 24px;
            outline: none;
            border: none;
            background: #FAFAFA;
        }

        .select {
            margin-left: 120px;
            margin-top: -40px;
        }
    }

    .arrow {
        margin: 25px 0 25px 50%;
    }

    .feetag {
        text-align: center;
        font-family: Helvetica;
        font-size: 14px;
        margin: 10px 0 40px 0;
    }

    .errmsg {
        text-align: center;
        font-family: Helvetica;
        font-size: 14px;
        font-weight: bold;
        margin: 10px 0 0 0;
        color: red;
    }

    .btcaddress {
        width: 55%;
        text-align: center;
        margin: 25px 0 25px 23%;
        border: none;
        outline: none;
        font-family: Helvetica;
        font-size: 24px;
        border-bottom: 1px solid;
    }

    .swapbutton {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        margin-top: 25px;
        margin-bottom: 25px;
        padding: 0 100px;
        height: 50px;
        background: #F7931A;
        border-radius: 25px;
        border-style: none;
        font-family: Helvetica;
        font-size: 16px;
        color: #FFFFFF;
        cursor: pointer;
        outline: none;
    }
}

@media screen and (max-width: 676px) {
    .swap {
        .bg {
            width: 380px;
            .input {
                width: 120px;
            }
    
            .select {
                margin-left: 75px;
            }
        }
    }
}

@media screen and (max-width: 479px) {
    .swap {
        .bg {
            width: 300px;
            .input {
                width: 80px;
                font-size: 20px;
            }
    
            .select {
                margin-left: 45px;
            }
        }

        .btcaddress {
            font-size: 20px;
        }
    }
}

@media screen and (max-width: 320px) {
    .swap {
        .bg {
            padding: 15px 30px;
            width: 260px;
    
            .input {
                width: 80px;
                font-size: 20px;
            }
    
            .select {
                margin-left: 30px;
            }
        }

        .btcaddress {
            font-size: 18px;
        }
    }
}

