.about {
   padding: 40px 0;
   background: #FAFAFA;

   .about-div {
      width: 70%;
      margin: 0 15%;
      display: grid;
      grid-template-columns: repeat(2,1fr);
      align-items: flex-start;
      justify-content: center;
      grid-gap: 40px;
      h2 {
         font-size: 1.5rem;
         text-align: center;
         margin-bottom: 20px;
         padding: 0 auto;
      }
      p {
         padding-top: 5px;
         line-height: 1.6;
         text-align: center;
      }
    }
}

@media screen and (max-width:785px){
   .about {
      .about-div {
         h2 {
            font-size: 1.2rem;
         }
      }
   }
}

@media screen and (max-width: 599px) {
   .about {
      .about-div {
         grid-template-columns: 1fr;
         h2 {
            font-size: 1.3rem;
         }
      }
   }
}