.header {
    .errMsg {
        width: 100%;
        color: #FFFFFF;
        background-color: #F7931A;
        text-align: center;
        padding: 5px 0px;
        font-family: Helvetica;
    }

    .dappHeader {
        width: 80%;
        padding: 30px 30px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-left: 10%;

        img {
            margin: 0;
            width: 20%;
            height: 10%;
            max-width: 200px;
        }

        button {
            padding: 0 30px;
            height: 40px;
            font-size: 14px;
            border-radius: 21.5px;
            border: 2px solid #F7931A;
            font-family: Helvetica;
            background-color: #FFFFFF;
            color: #F7931A;
            text-align: center;
            cursor: pointer;
            outline: none;
        }

        text {
            padding: 10px 30px;
            height: 40px;
            font-size: 14px;
            min-width: 153px;
            border-radius: 21.5px;
            border: 2px solid #F7931A;
            font-family: Helvetica;
            background-color: #FFFFFF;
            color: #F7931A;
            text-align: center;
            cursor: pointer;
            outline: none;
        }
    }
}

@media screen and (max-width: 1024px) {
    .header {
        .dappHeader {
            img {
                width: 25%;
            }


        }
    }
}

@media screen and (max-width: 768px) {
    .header {
        .dappHeader {
            img {
                width: 30%;
            }
        }
    }
}

@media screen and (max-width: 479px) {
    .header {
        .dappHeader {
            img {
                width: 50%;
            }
            button {
                display: none;
            }
            .addressTag {
                display: none;
            }
        }
    }
}

@media screen and (max-width: 320px) {
    .header {
        .dappHeader {
           img {
                width: 60%;
            }
        }
    }
}