.footer {
    width: 100%;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px 25px 10px 25px;
    position: fixed;
    &-logo {
        width: 50px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin: 0 10px;
        cursor: pointer;
    }
}